import React, { useState } from "react";
import axios from 'axios';

const Home = function (props) {

    const [state, setState] = useState({
        username: "",
        password: "",
        email: "",
        isLogin: false,
    });

    const RegistrarUsuario = async function () {
        const form = {username:state.username, password:state.password};
        axios.post("/api/test/register", form).then((response)=>{
            alert(response.data.message);
            setState({...state, isLogin:response.data.status});
        });
    };

    const IniciarSesion = async function () {
        const form = {username:state.username, password:state.password};
        axios.post("/api/test/login", form).then((response)=>{
            alert(response.data.message);
            setState({...state, isLogin:response.data.status});
        });
    };

    const Change = function (e) {
        setState({ ...state, [e.target.name]: e.target.value })
    };

    return (
        <div className="container">
            <div className="row justify-content-center">
                <div className="col-md-6">
                    <div className="form-group">
                        <h4>Fakelogin</h4>
                    </div>
                    <div className="form-group">
                        <label>Username = {state.username}</label>
                        <input type="text" name="username" onChange={Change} value={state.username} className="form-control" />
                    </div>
                    <div className="form-group">
                        <label>Password = {state.password}</label>
                        <input type="password" name="password" onChange={Change} value={state.password} className="form-control" />
                    </div>
                    <div className="form-group">
                        <button onClick={RegistrarUsuario} className="btn btn-success form-control">Registrar usuario</button><br/><br/>
                        <button onClick={IniciarSesion} className="btn btn-success form-control">Iniciar sesi√≥n</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Home;